/*--------------------------------------------------------------
# Typography * Базовые стили шаблона
--------------------------------------------------------------*/
html, body {
  margin: 0 auto;
  padding: 0 0 0 0;
  width: 100%;
  height: 100%;
  min-width: 100%;
  min-height: 100%;
  color: #001535;
  font-family: 'Montserrat', 'Arial', sans-serif;
  scroll-behavior: smooth; }

main {
  position: relative;
  width: 100%;
  overflow: hidden;
  font-size: 16px;
  line-height: 20px; }

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 1em;
  font-family: 'Montserrat-Bold', 'Arial', sans-serif;
  font-weight: bold;
  font-style: normal;
  text-align: center;
  text-transform: uppercase; }

ul {
  list-style: none;
  margin: 0;
  padding: 0; }

a {
  text-decoration: none;
  color: inherit; }

figure {
  margin: 0; }

img {
  display: block;
  max-width: 100%;
  height: auto; }

.container {
  width: 90%;
  max-width: 1280px;
  margin: 0 auto; }

/*Сетка
  xs- сетка для мобильных
  xm - сетка для планшетов
  xl - сетка для экранов
*/
.success-text {
  color: #73AF00; }

.flex-wrap {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between; }

.flex-box {
  display: flex;
  align-items: center;
  justify-content: space-between; }

.section {
  padding: 1em 0; }
  .section--gray {
    background-color: #EEF0F4; }

.section-title {
  font-size: 2em;
  line-height: 1.5em; }

/* Для Chrome */
input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  margin: 0; }

/* Для Firefox */
input[type=number] {
  -moz-appearance: textfield; }

